.navbar{
    font-family: 'Poppins', sans-serif;
    padding: 10px;
    font-weight: 900;
    color: white;
    font-size: 18px;
}

.nav-link{
    padding-right: 20px;
    margin-right: 25px;
    font-weight: bold;
    color: white;
}


.nav-link:hover{
    transform: scale(1.30);
    font-weight: bold;
    color: white;
}


.img-Homepage{
    width: 50%;
    height: 70%;
    padding: 10px;
    margin-bottom: 10px;
    margin-top: 50px;
}

.description{
    text-align: center;
    font-family: 'Nunito', sans-serif;
    font-weight: 900;
    font-size: 24px;
}

.foooter{
    background-color: none;
    color: white;
    font-style: italic;
    font-size: 12px;
    font-family: 'Poppins', sans-serif;
}

.img-Saturday{
    width: 75%;
    height: 75%;
    margin-top: 50px;
    margin-bottom: 10px; 
}

h3{
    text-align: center;
    font-family: 'Nunito', sans-serif;
    font-weight: 900;
}

.content{
    text-align: justify;
    font-family: 'Nunito', sans-serif;
    font-weight: 900;
    font-size: 18px;
}

.img-Sunday{

    margin-top: 50px;
    margin-bottom: 10px; 
    width: 75%;
    height: 75%;
}

.breadcrumb{
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    font-weight: 900;
    color: black;
}

.linkss{
    background-color: #EAEAEA;
    padding: 10px;
    border-radius: 25px;
