}

body{
    background-color: #EAEAEA;
    background-image: url("../img/gif.gif");
}

.jumbotron{
    border: 2px solid white;
    padding: 25px;
    border-radius: 25px;
    box-shadow: 10px 8px 10px #888888;
    margin-top: 125px;
    background-color: #EAEAEA;
    margin-bottom: 250px;
}

.lead{
    font-family: 'Nunito', sans-serif;
    font-style: italic;
