    width: 50%;
    height: 50%;
}

.card{
    width: 65%;
    height: 45%;
    margin: auto;
    border-radius: 20px;
    padding: 15px;
    box-shadow: 10px 8px 10px #252525;
}

.card-title, .card-text{
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}

.card-title{
    font-family: 'Nunito', sans-serif;
    text-align: center;
    font-size: 30px;
}

.card-cleaning{
    width: 55%;
    height: 40%;
}

.card-workout{
    width: 55%;
    height: 40%;
}

.card-donut{
    width: 55%;
    height: 40%;
}

.card-bebi{
